.headerAccordion {
  height: 47px;
  background-color: #ff6060;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 6px 30px 6px 18px;
  border-radius: 5px;
  cursor: pointer;
  position: relative;
}

.titleAccordion {
  color: #ffffff;
  font-size: 24px;
  font-weight: 500;
  line-height: 142.6%;
}

.iconAccordion {
  height: 16px;
  width: 27px;
  margin: 0 12px 0 0;
}

.contentAccordion {
  color: #ff6060;
  background-color: #e5e5e5;
  font-size: 24px;
  font-weight: 400;
  line-height: 142.6%;
  padding: 23px 18px 18px 18px;
  border-radius: 0px 0px 5px 5px;
}

@media (min-width: 769px) and (max-width: 992px) {
  .headerAccordion {
    height: 40px;
    padding: 6px 10px 6px 12px;
  }

  .titleAccordion {
    font-size: 18px;
  }

  .iconAccordion {
    height: 18px;
  }

  .contentAccordion {
    font-size: 18px;
  }
}

@media (max-width: 768px) {
  .headerAccordion {
    height: 30px;
    padding: 0px 0px 0px 12px;
  }

  .titleAccordion {
    font-size: 13px;
  }

  .iconAccordion {
    width: 18px;
  }

  .contentAccordion {
    font-size: 12px;
  }
}
