.bannerAbout {
  height: 223px;
  width: 100%;
  border-radius: 25px;
  overflow: hidden;
  background-image: url('./../../assets/kalen-emsley-Bkci_8qcdvQ-unsplash\ 2.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
}

.bannerAbout::before {
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0.3;
}

.accordionAbout {
  margin-block: 31px 70px;
  margin-inline: auto;
  padding-inline: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 31px;
}

@media (min-width: 769px) and (max-width: 992px) {
  .accordionAbout {
    gap: 25px;
    margin-block: 30px 50px;
    padding-inline: 60px;
  }
}

@media (max-width: 768px) {
  .bannerAbout {
    border-radius: 10px;
  }

  .accordionAbout {
    gap: 20px;
    margin-block: 20px 50px;
    padding-inline: 0px;
  }
}
